h1, h2, h3, h4, h5, h6, ul, ol, p {
  margin: 0;
}

li {
  margin: 0;
  padding: 0;
}

.container {
  width: 95%;
  margin: 0 auto;
}

.section {
  background-color: ivory;
  border-radius: 5px;
  outline: 1px solid #add8e6;
  margin-bottom: 5px;
  padding: 5px 20px;
}

.title {
  color: #4169e1;
}

.nav {
  display: flex;
}

.alert {
  color: #2a2a2a;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  margin: 0;
  padding: 24px;
  font-size: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: translateX(calc(100% + 20px));
}

.alert.success {
  color: #fff;
  background-color: #4caf50;
  border: 1px solid #388e3c;
}

.alert.is-visible {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

.timer {
  text-align: center;
  color: #212121;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
}

.clockface {
  color: #3f51b5;
  margin-top: 0;
  margin-bottom: 24px;
  font-family: Roboto Mono, monospace;
  font-size: 80px;
}

.timer-btn {
  min-width: 140px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: 0;
  border-radius: 3px;
  justify-content: center;
  margin-left: 4px;
  margin-right: 4px;
  padding: 10px 20px;
  font-size: 24px;
  display: inline-flex;
}

.timer-btn.is-active {
  background-color: #2196f3;
}

.clock {
  width: 550px;
  height: 550px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("../../../clock5.0c58168f.jpg");
  background-size: cover;
  border-radius: 50%;
  padding-bottom: 7px;
  position: relative;
  left: 550px;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 268px;
  left: 268px;
}

.clock-seconds__arrow {
  width: 5px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 7px;
  height: 29%;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 12px;
  height: 22%;
  bottom: 50%;
  left: 50%;
}

.date {
  width: 100px;
  height: 100px;
  color: #000;
}

.calendar {
  justify-content: space-between;
  display: flex;
}

.date-style {
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 40px;
  font-weight: 900;
  display: flex;
}

.digital-clock {
  text-align: center;
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 60px;
}

